*{
    font-family: Arial, Helvetica, sans-serif;
    background-color: bisque;
}

body{
    padding: 60px;
}

input{
    font-style: bold;
    color: #1a1818;
    padding: 10px;
    width: 100%;
    font-size: 16px;
    font-family: Raleway;
    border: 1px solid #aaaaaa;
    border-radius: 5px;
}
.gifs-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
    margin-top: 20px;
}

.main-container{
    align-content: center;
    border: 1px solid #aaaaaa;
    border-radius: 6px;
    margin-bottom: 5px;
    margin-right: 10px;
    overflow: hidden;
}

.main-container:hover{
    box-shadow: 0px 0px 10px #aaaaaa;
}

.main-container p{
    font-style: italic;
    font-size: 40px;
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-family: Raleway;
}

.main-container img{
    width: 100%;
    max-height: 200px;
}


